<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>权限管理</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-table
        :data="rightList"
        style="width: 100%"
        border
        stripe
        >
        <el-table-column type="index"
        label="#">
        </el-table-column>
        <el-table-column
        prop="authName"
        label="权限名称"
        >      
        </el-table-column>
        <el-table-column
        prop="path"
        label="路径"
        >      
        </el-table-column>
        <el-table-column
        prop="level"
        label="权限等级"
        >
        <template v-slot="scope">
          <el-tag v-if="scope.row.level === '0'">一级</el-tag>
          <el-tag type="success" v-else-if="scope.row.level === '1'" >二级</el-tag>
          <el-tag type="warning" v-else>三级</el-tag>
        </template>      
        </el-table-column>
      </el-table> 
    </el-card>
  </div>
</template>
<script>
export default {
    data(){
      return {
        rightList: []
      }
    },
    created() {
      // 获取所有权限
      this.getRightList();
    },
    methods: {
     async getRightList() {
        const {data: res} = await this.$http.get('/right/getRights');
        if(res.code !== 0) return this.$message.error('获取数据失败');
        this.rightList = res.data;
        console.log(res);
      }
    }

}

</script>
